let app = require("@/config");
let enums = require("../enumsv1.js");
export default {
	onShow() {
		this.getData();
	},
	onLoad() {
	},
	methods: {
		getData() {
			uni.post("/api/workflow/AllFlow", {name:'按团队申请实验室'}, msg => {
					this.data = msg.data;
			})
		},
		toExecute(item) {
			item.StepId = undefined;
			if (item.WorkflowName === "按团队申请实验室") {
				uni.setStorage({
					key: 'jmpInfo',
					data: item,
					success: () => { //如果缓存成功则跳转
						uni.navigateTo({
							url: './roomFlowsCtrl'
						})
					}
				})
			} else if (item.WorkflowName === "按机位申请实验室") {
				uni.setStorage({
					key: 'jmpInfo',
					data: item,
					success: () => { //如果缓存成功则跳转
						uni.navigateTo({
							url: './seatFlowsCtrl'
						})
					}
				})
			}
		},
		foldUp() {
			this.display = !this.display
		}
	},
	data() {
		return {
			workflow: enums.workflow,
			wColor: enums.workflowColor,
			icon: app.webInfo.avatar,
			page: 1,
			pageSize: 10,
			data: [],
			modalName: null,
			listTouchStart: 0,
			listTouchDirection: null,
			display: true
		}
	}
}
